import React from "react";
import Grid, { GridSatuses } from "./Grid";
import Chrono from "./Chrono";
import Dialog from "./Dialog";

import { formatUserTime } from "../tools";

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            showHelp: false,
            confirmReset: false,
            confirmSolve: false,
            showResult: false,
            status: GridSatuses.initial
        };

        this.gridElement = React.createRef();
        this.chronoElement = React.createRef();

        this.handleCheckClick = this.handleCheckClick.bind(this);
        this.handleSolveClick = this.handleSolveClick.bind(this);
        this.handleResetClick = this.handleResetClick.bind(this);
        this.startPlaying = this.startPlaying.bind(this);
    }

    handleCheckClick() {
        if (this.gridElement.current.check()) {
            this.setState({ status: GridSatuses.solved });
            this.chronoElement.current.stop();
        }

        this.setState({ showResult: true });
    }

    handleSolveClick() {
        this.gridElement.current.solve();
        this.chronoElement.current.stop();

        this.setState({ confirmSolve: false, status: GridSatuses.solved });
    }

    handleResetClick() {
        this.gridElement.current.reset();

        if (this.gridElement.current.state.status === GridSatuses.solved) {
            this.chronoElement.current.reset();
        }

        this.setState({ confirmReset: false, status: GridSatuses.initial });
    }

    startPlaying() {
        this.setState({ status: GridSatuses.playing });
    }

    render() {
        const helpButtons = [{
            label: 'Mdel',
            iconCls: 'cross',
            handler: () => this.setState({ showHelp: false })
        }];

        const checkButtons = [{
            label: 'Mdel',
            iconCls: 'cross',
            handler: () => this.setState({ showResult: false })
        }];

        const solveButtons = [{
            label: 'Ih',
            iconCls: 'tick',
            handler: this.handleSolveClick
        }, {
            label: 'Ala',
            iconCls: 'cross',
            handler: () => this.setState({ confirmSolve: false })
        }];

        const resetButtons = [{
            label: 'Ih',
            iconCls: 'tick',
            handler: this.handleResetClick
        }, {
            label: 'Ala',
            iconCls: 'cross',
            handler: () => this.setState({ confirmReset: false })
        }];

        return (
            <div className="app">
                <h2 className="title">AWALEN S YINECCABEN  {this.props.index + 1}</h2>

                <div className="actions">
                    <button className="general" onClick={ () => this.setState({ showHelp: true }) }>
                        <span className="icon help">Ilugan</span>
                    </button>
                    <Dialog show={this.state.showHelp}
                            title="Ilugan"
                            buttons={helpButtons}
                            onClose={ () => this.setState({ showHelp: false }) }>
                        <p>Tibuda n wawalen ara d-tafe deg wurar-a, ttunefkent-d deg texxamin yesan ineccaben.
                        Awalen ilaq ad ttwarun seg uzelma ar uyeffus ne seg usawen ar ukessar as ma yella
                        uneccab-nsen iwehha er tama tanmeglant.</p>
                        <ul>
                            <li>Tekki ef texxamin tilmawin akken ad taru asekkil i tebi. Tzemre ad tselu ger texxamin s teqfilin n tnila n unasiw. Sseqdec tiqfilin "" d "Suppr" ma tebi ad tsefe isekkilen.</li>
                            <li>Mi tfukke urar, tekki ef "Senqed" ad twali ma tufi-d akk awalen.</li>
                            <li>Tzemre ad twali tifrat melmi i tebi s teqfilt "Tifrat" (dacu kan e-it i taggara mi ara yebdu yettfuu walla).</li>
                            <li>Tzemre ad talse i wurar melmi i tebi s teqfilt "Ales-as". ur-k, ayen akk i turi ad iu!</li>
                            <li>Ma ixu-ik unasiw, aru akka: e_ =  | c_ =  | d_ =  | g_ =  | h_ =  | q_ =  | r_ =  | s_ =  | z_ = . Urar kan!</li>
                            <li>Inegzumen n wakud i nesseqdec: "s" d isragen ne d tisatin, "d" d tisdidin ne ddqayeq, "n" d tasinin ne d isugunden.</li>
                        </ul>
                        <p>Ma temlale-d kra n tucca ne tebi ad a-d-tazne isumar, aru-ya-d tamawt deg GitHub seg yiccer <a href="https://github.com/nabil509/kab-arrow-words/issues" target="_blank">Issues</a> n usenfa ne aru-ya-d er "nabil509 at gmail dot com". Atan wurar ur-k!</p>
                    </Dialog>

                    <Chrono ref={this.chronoElement} />

                    <button className="general"
                            onClick={this.handleCheckClick}
                            disabled={this.state.status === GridSatuses.solved}>
                        <span className="icon tick">Senqed</span>
                    </button>
                    <Dialog show={this.state.showResult}
                            title="Asenqed"
                            buttons={checkButtons}
                            onClose={ () => this.setState({ showResult: false }) }>
                        <p>
                            {
                                this.state.status === GridSatuses.solved ?
                                "Tufi-d kullec, igerrez! Tefri-tt deg " + formatUserTime(this.chronoElement.current.state.time) + "." :
                                "Mazal ur d-tufi ara kullec. Aha rnu kra uxemmem, afud!"
                            }
                        </p>
                    </Dialog>

                    <button className="general"
                            onClick={ () => this.setState({ confirmSolve: true }) }
                            disabled={this.state.status === GridSatuses.solved}>
                        <span className="icon wand">Tifrat</span>
                    </button>
                    <Dialog show={this.state.confirmSolve}
                            title="Tifrat"
                            buttons={solveButtons}
                            onClose={() => this.setState({ confirmSolve: false })}>
                        Dayen yakan tebi ad twali tifrat?
                    </Dialog>

                    <button className="general"
                            onClick={() => this.setState({ confirmReset: true })}
                            disabled={this.state.status === GridSatuses.initial}>
                        <span className="icon arrow-undo">Sfe</span>
                    </button>
                    <Dialog show={this.state.confirmReset}
                            title="Asfa"
                            buttons={resetButtons}
                            onClose={() => this.setState({ confirmReset: false })}>
                        D tidet tebi ad tsefe akk ayen i turi?
                    </Dialog>
                </div>

                <Grid data={this.props.data}
                      status={GridSatuses.initial}
                      ref={this.gridElement}
                      onStartPlaying={this.startPlaying} />
            </div>
        );
    }
}

export default App;
